import React from 'react';
import { Grid, Typography, Button, Box } from '@mui/material';
import myteam from '../images/myteam.jpg';
import chip from '../images/heroPicture.png';
import useStyles from '../styles/styles';
import Fade from '@mui/material/Fade';
import Slide from '@mui/material/Slide';

const Hero = (props) => {
  const classes = useStyles();
  const onClick = () => {
    props.onClick();  
  }
  
  return (
    <Box className={classes.heroBox}>
      <Grid container spacing={6} className={classes.gridContainer}>
        <Grid item xs={12} md={7}>
          <Typography variant="h6" fontWeight={700} className={classes.title}>
            Teknistä osaamista ja maalaisjärkeä yhdistäen.
          </Typography>
          <Typography variant="h6" className={classes.subtitle}>
            Meillä on vuosien kokemus sulautetuista järjestelmistä ja elektroniikan suunnittelusta, mutta ei pelkkää nörttien puuhastelua - ymmärrämme myös, miten asiakkaidemme bisnes pyörii. Saat meiltä kokonaisvaltaiset, järkevät ratkaisut, jotka tukevat yrityksesi tavoitteita ja toimivat niin kuin pitää.
          </Typography>
          
          <Button
            onClick={onClick}
            variant="contained"
            color="primary"
            sx={{ width: '200px', fontSize: '16px' }}
          >
            OTA YHTEYTTÄ !
          </Button>
        </Grid>
        <Slide in={true} direction="left" timeout={1000}>
        <Grid item xs={12} md={5}>
          <img src={chip} alt="My Team" className={classes.largeImage} />
        </Grid>
        </Slide>
      </Grid>
    </Box>
  );
};

export default Hero;